.condition-check label:before {
  background: transparent;
  content: "";
  height: 16px;
  left: 0;
  position: absolute;
  border-radius: 3px;
  top: 3px;
  width: 16px;
  border: 2px solid #ababb1;
  transition: all 0.3s ease;
}

.condition-check input:checked + label::after {
  background: transparent;
  bottom: 0;
  font-family: "Font Awesome 5 Pro";
  content: "\f00c";
  color: #2b70fa;
  font-size: 10px;
  left: 3px;
  position: absolute;
  top: -1px;
  transition: all 0.3s ease;
}

